:root{

  --primaryColor1: 0, 26,119;
  --primaryColor2: 0, 71, 255;
  --secondayColorSuccess: 66, 173, 0;
  --secondaryColorAlert : 219, 79, 0;
  --clickableColor : 255, 214, 0;
  --clickableColorHover : 255, 163, 0;
  --lightGray : 217, 217, 217;
  --darkGray : 67, 67, 67;
  --white : 255, 255, 255;
  --black : 0, 0, 0;
}

body{
  width: 100vw;
  height: 100vh;
  display: grid;
  justify-content: start stretch;
  align-items: start stretch;
  box-sizing: border-box;
  overflow: hidden;
  color: rgba(var(--lightGray));
}

h3{
  color: rgba(var(--lightGray));
  padding: 10px 0;
  font-weight: normal;
}
h3 span{
  color:rgba(var(--clickableColor));
  font-weight: bold;
}
.displayScreen{
  height: 100vh;
  display: grid;
  justify-content: start stretch;
  align-items: start stretch;
  
}

.bgMain{
  background: linear-gradient( rgba(var(--primaryColor1)), rgba(var(--primaryColor2)));
}

.bgSuccess{
  background: linear-gradient( rgba(var(--primaryColor1)), rgba(var(--secondayColorSuccess)));
}

.bgGray{
  background: linear-gradient( rgba(var(--darkGray)), rgba(var(--lightGray)));
}

.bgBtn{
  background: linear-gradient( rgba(var(--clickableColor)), rgba(var(--clickableColorHover)));
  padding: 10px 15px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  width: auto;
  transition-duration: .5s;
  color: rgba(var(--darkGray));
}

.bgBtn:hover{
  background: linear-gradient( rgba(var(--clickableColor)), rgba(var(--clickableColorHover)), rgba(var(--clickableColorHover)));
  
}

.roundBtn{
  border-radius: 50%;
  position: absolute;
  right: 10px;
  top: 10px;
}

.dashboard{
  /* width: 100vw;
  height: 100vh; */
  display: grid;
  grid-template-rows: 40px auto;
  justify-content: start stretch;
  align-items: start stretch;
  padding: 30px;
  gap: 10px;
 
}
.nav{
  background: rgba(var(--black), .1);
  border-radius: 5px;
  padding: 0 10px;
  display: grid;
  grid-template-columns: auto 120px;
  justify-content: start stretch;
  align-items: center;
  color: rgba(var(--lightGray));
}
.contentCenter{
  align-self: center;
  display: grid;
  grid-template-rows: auto auto;
  justify-content: center;
  align-items: center;
  gap: 10px;
  
}
.reports{
  background: rgba(var(--black), .1);
  border-radius: 5px;
  padding: 20px;
  display: grid;
  gap: 10px;
  grid-template-columns: 1fr 1fr;
  justify-content: start stretch;
  align-items: center;
  color: rgba(var(--lightGray));
}
.modal{
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: rgba(var(--black), .8);
  display: flex;
  justify-content: center;
  align-items: center;
 
}

.modalPanel{
  position: relative;
  width: 60%;
  height: 350px;
  border: 1px solid white;
  border-radius: 20px;
  display: grid;
  grid-template-rows: 80% 20%;
  justify-content: center;
  align-items: center;
}
.login{
  background: rgba(var(--black), .1);
  border-radius: 5px;
  padding: 20px;
  text-align: center;
}

form{
  width: 200px;
  padding: 20px;
  display: grid;
  grid-template-rows: auto auto auto;
  gap: 5px;
  justify-content: start stretch;
  align-items: start;
  
}

input{
  padding: 8px;
  border-radius: 5px;
}

.servicePage{
  width: 80%;
}
.serviceList{
  
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  gap: 10px;
  
}

.service{
  background: rgba(var(--black), .1);
  display: grid;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  transition-duration: .5s;
  cursor: pointer;
  padding: 0 20px;
}

.service:hover{
  background: rgba(var(--black), .3);
  
}
.service h4{
  text-align: center;
}

.scanList{
  display: grid;
  gap: 1px;
}

.scanForm{
  /* height: 40px; */
  width: 350px;
  display: grid;
  grid-template-columns: 250px 100px;
  gap: 1px;
  align-items: center;
  background: rgba(var(--black), .1);
  padding: 10px 15px;
  border-radius: 5px;
  transition-duration: .5s;
}
.scanForm:hover{
  
  background: rgba(var(--black), .3);
  
}

.docName{
  /* height: 40px; */
  display: flex;
  align-items: center;
  color: rgba(var(--lightGray));
  /* padding: 0 15px; */
  
}
.scanStatus{
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-rows: 50px 200px;
  gap: 10px;
}

.scanner{
  justify-self: center;
  width: 100px;
  height: 100px;
  background: rgba(var(--black), .5);
  padding:  25px 15px;
  border-radius: 5px;
  display: grid;
  align-items: center;
  gap: 1px;
}

.bar{
  background: rgba(var(--black), .3);
  width: 100px;
  height: 10px;
  animation: scanner 1s linear infinite ;
}

@keyframes scanner{
  0% {background: rgba(var(--secondayColorSuccess), .3)}
  40% {background: rgba(var(--secondayColorSuccess), .8)}
  100% {background: rgba(var(--secondayColorSuccess), .8)}
}